import React from 'react';
import './App.css';
import Titles from './components/Titles';
import Form from './components/Form'
import Weather  from "./components/Weather";
import { grid,Col,code,Row } from 'reactstrap';

const API_KEY = "32db9e6a8bc974b64076f9d80b0724c2";



class App extends React.Component {

  state = {
    temperature:undefined,
    humidity:undefined,
    pressure:undefined,
    temp_max:undefined,
    temp_min:undefined,
    city:undefined,
    country:undefined,
    speed:undefined,
    deg:undefined,
    description:undefined,
    error:undefined
  }

  render(){
    return (    
    <div className="wrapper">
      <grid className="grid-body">
        <Row>
          <Col xs={12} md={4} sm={4} lg={4} className="padding-0">
            <div className="title-container">
              <Titles />
            </div>
          </Col>
          <Col xs={12} md={8} sm={8} lg={8} className="padding-0">
            <div className = "form-container ">
              <Form getWeather={this.getWeather}/>
                <Weather 
                  temperature = {this.state.temperature}
                  city = {this.state.city}
                  humidity = {this.state.humidity}
                  country = {this.state.country}
                  description = {this.state.description}
                  pressure = {this.state.pressure}
                  temp_min = {this.state.temp_min}
                  temp_max = {this.state.temp_max}
                  speed = {this.state.speed}
                  deg = {this.state.deg}
                  error = {this.state.error}
                />
            </div>
          </Col>
        </Row>
      </grid>
    </div>
    );
  }
  // <Col xs={12} md={3} >
  // <Titles />
  //   <Form getWeather={this.getWeather}/>
  //   <Weather 
  //     temperature = {this.state.temperature}
  //     city = {this.state.city}
  //     humidity = {this.state.humidity}
  //     country = {this.state.country}
  //     description = {this.state.description}
  //   />
  // </Col>

 

  getWeather = async (e) => {
    e.preventDefault();
    const city = e.target.city.value;
    const country = e.target.country.value;
    if(city && country) {
      const api_call = await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city},${country}&appid=${API_KEY}&units=metric`);
      const data = await api_call.json();
      console.log(data );
      this.setState({
        temperature:data.main.temp,
        humidity:data.main.humidity,
        city:data.name,
        country:data.sys.country,
        description:data.weather[0].description,
        pressure:data.main.pressure,
        temp_min:data.main.temp_min,
        temp_max:data.main.temp_max,
        speed:data.wind.speed,
        deg:data.wind.deg,
        error:undefined
      });
    }else{
      console.log("error");
      this.setState({
        temperature:undefined,
        humidity:undefined,
        city:undefined,
        country:undefined,
        description:undefined,
        pressure:undefined,
        temp_min:undefined,
        temp_max:undefined,
        speed:undefined,
        deg:undefined,
        error:"Please enter the city and country name."
      });
    }
  }
}

export default App;


