import React from 'react';

class Weather extends React.Component {

    render(){
        return (
            <div>
                {
                    <p className = "weather__error">
                      {this.props.error} 
                    </p>
                }
                { this.props.city && this.props.country &&
                     <p className = "weather__value"> 
                        <span className="weather__key">Location: </span>{this.props.city},{this.props.country}
                     </p>
                }
                {   this.props.temperature &&
                     <p className = "weather__value">
                         <span className="weather__key">Temperature:         </span>{this.props.temperature}
                     </p>
                }
                {   this.props.humidity && 
                    <p className = "weather__value"> 
                       <span className="weather__key"> Humidity:         </span>{this.props.humidity}
                    </p>
                }
                {   this.props.description && 
                    <p className = "weather__value"> 
                      <span className="weather__key">Status:         </span>{this.props.description} 
                    </p>
                }
                {   this.props.pressure && 
                    <p className = "weather__value"> 
                      <span className="weather__key">pressure:         </span>{this.props.pressure} 
                    </p>
                }
                {   this.props.temp_max && 
                    <p className = "weather__value"> 
                      <span className="weather__key">temp_max:         </span>{this.props.temp_max} 
                    </p>
                }
                {   this.props.temp_min && 
                    <p className = "weather__value"> 
                      <span className="weather__key">temp_min:         </span>{this.props.temp_min} 
                    </p>
                }
                {   this.props.speed && 
                    <p className = "weather__value"> 
                      <span className="weather__key">Wind speed:         </span>{this.props.speed} 
                    </p>
                }
                {   this.props.deg && 
                    <p className = "weather__value"> 
                      <span className="weather__key">Wind degree:         </span>{this.props.deg} 
                    </p>
                }
                
            </div>
        );
    }

}

export default Weather;




